    </div>
    <div class="fz-18 mt-46 lh-36  cor-999999 ">Trong khi hai khía cạnh này phải hoàn toàn rõ ràng, Pionan cũng cung cấp giao dịch hợp đồng tương lai vĩnh viễn phức tạp hơn một chút. Hợp đồng tương lai vĩnh viễn là hợp đồng không có ngày thanh toán. Điều này có nghĩa là nếu bạn mua một hợp đồng như vậy, bạn có thể giữ nó bao lâu tùy thích, không ai thúc ép bạn bán nó.</div>
    <div class="fz-18 mt-30 lh-36  cor-999999 ">Tuy nhiên, điều quan trọng cần đề cập là khi nói đến giao dịch hợp đồng tương lai vĩnh viễn, thường có một số điều kiện nhất định cần được đáp ứng nếu bạn muốn duy trì hợp đồng.</div>
    <div class="fz-18 mt-27 lh-36  cor-999999 ">Các hợp đồng tương lai dài hạn với đòn bẩy thậm chí còn khó nắm bắt hơn, và chắc chắn là thậm chí còn rủi ro hơn. Điều đó có nghĩa là bạn có thể mở một vị trí có giá trị cao hơn số dư tài khoản thực tế của mình. Vì Pionan cung cấp đòn bẩy lên đến x100, điều đó có nghĩa là bạn có thể tận dụng số tiền ký quỹ ban đầu của mình lên đến 100 lần - 5000$ thay vì 50$.</div>
